<template>
  <div>
    <div class="box" v-if="questions.questions.length != 0">
      <div class="wrapper">
        <h3 style="color: white; font-family: philosopher">
          Mieux connaître la faune marine
        </h3>
        <!-- {{ questions }} -->
        <question
          :question="questions.questions[0]"
          @reponse="resultat"
          :confirmer="ready_next"
          :win="win"
        >
          <a
            href="#"
            v-if="questions.questions.length != 1 && ready_next"
            @click="next()"
          >
            <span>Prochaine question</span>
          </a>
          <a
            href="#"
            v-if="questions.questions.length == 1 && ready_next"
            @click="next()"
          >
            <span>Voir résultat</span>
          </a>
        </question>
      </div>
    </div>
    <div v-else class="wrapper_resultat">
      <div class="left">
        <p class="text">Votre score</p>
        <p class="number">
          <span>{{ final }}</span
          >/{{ nombre_question }}
        </p>
      </div>
      <div class="right">
        <p class="bien">Bien joué !</p>
        <p class="text">
          N’hésitez pas à partager vos connaissances avec votre entourage. C’est
          ainsi que l’on fera évoluer la situation à laquelle font face les
          cétacés de Méditérrannée !
        </p>
        <img src="../assets/bulle.png" alt="bulle" />
        <img src="../assets/Whale.png" alt="whale" />
        <a href="#" @click="refresh()">
          <span>Partager mon score</span>
        </a>
      </div>
    </div>
    <!-- <input type="button" value="Retour au début" @click="refresh()" /> -->
  </div>
</template>

<script>
import questions from "../assets/questions.json";
import question from "./question.vue";
export default {
  components: {
    question,
  },
  data() {
    return {
      questions: questions,
      reponse_actuelle: null,
      save: [],
      final: 0,
      nombre_question: 0,
      ready_next: false,
      win: false,
    };
  },
  mounted() {
    this.nombre_question = this.questions.questions.length;
  },
  methods: {
    next() {
      this.ready_next = false;
      this.win = false;
      this.questions.questions.shift();
    },
    resultat(data) {
      if (this.ready_next == false) {
        if (this.questions.questions[0].answer == data) {
          this.final++;
          this.win = true;
        }
      }

      this.ready_next = true;
    },
    refresh() {
      document.location.reload();
    },
  },
};
</script>

<style scoped lang="scss">
.box {
  margin-top: 60px;
  .wrapper {
    h3 {
      text-align: left;
      margin-bottom: 41px;
    }
    button {
      margin-top: 1em;
    }
  }
}

.wrapper_resultat {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  .left {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p {
      // margin: auto;
      margin-top: 12px;
      font-family: philosopher;
      font-weight: bold;
    }
  }
  .right {
    a {
      position: absolute;
    }
  }
}
a {
  display: block;
  // position: absolute;
  z-index: 1000;
  background-color: #22b5ae;
  width: 180px;
  border-radius: 50px;
  // left: 40%;
  margin-top: 20px;
  span {
    line-height: 46px;
    color: white;
    font-weight: bold;
    font-size: 16px;
    font-family: OpenSans;
    font-family: philosopher;
    font-weight: bold;
  }
}
@keyframes animate {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: 118px;
  }
}
@media only screen and (max-width: 768px) {
  .box {
    margin-top: 60px;
    .wrapper {
      h3 {
        margin-left: 50px;
        text-align: left;
      }
    }
    button {
      margin-bottom: 6em;
    }
  }
  .wrapper_resultat {
    flex-direction: column;
    .left {
      width: 80%;
      height: 80%;
      margin: 50px 50px;
      .text {
        font-size: 35px;
      }
      .number {
        font-size: 35px;
        span {
          font-size: 60px;
        }
      }
    }
    .right {
      margin-left: 0;

      .bien {
        position: absolute;
        // margin-left: auto;
        bottom: 388px;
        left: 37%;
        font-size: 20px;
        font-weight: bold;
      }
      .text {
        position: absolute;
        bottom: 262px;
        left: 50px;
        text-align: left;
        width: 286px;
        height: 114px;
      }
      p {
        z-index: 1000;
        color: black;
        font-family: philosopher;
      }
      img {
        width: 95%;
      }
      a {
        bottom: 35%;
        right: 50px;
      }
    }
  }
}
@media only screen and (min-width: 768px) {
  .box {
    .wrapper {
      margin-left: 20%;
