        }
      }
    }
    .right {
      margin-left: 50px;
      a {
        top: 255px;
        right: 25%;
      }
      .bien {
        position: absolute;
        // margin-left: auto;
        top: 18%;
        right: 31%;
        font-size: 22px;
        font-weight: bold;
      }
      .text {
        position: absolute;
        top: 170px;
        right: 350px;
        text-align: left;
        width: 325px;
